.NavigationItem {
    margin: 10px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    width: 100%;
}

.NavigationItem a {
    color: gray;
    text-decoration: none;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
    color: black;
}


@media (min-width: 500px) {
    .NavigationItem {
        margin: 0;
        display: -ms-flexbox;
        display: flex;
        height: 100%;
        width: auto;
        -ms-flex-align: center;
            align-items: center;
    }
    
    .NavigationItem a {
        color: gray;
        height: 100%;
        padding: 26px 10px;
        border-bottom: 4px solid transparent;
    }

    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active {
        background-color: gray;
        border-bottom: 4px solid black;
        color: white;
    }
}


